






























import { Component, Vue } from "vue-property-decorator";
import Message from "../components/Message.vue";
import { MyHero } from "../Hero";

@Component({
  components: {
    Message
  }
})
export default class HeroLists extends Vue {
  private lists: MyHero[] = [];
  private title: string = "My Heroes";
  private newHero: string = "";
  public goDetail(id: number): void {
    this.$store.commit("addMessage", `fetched hero id=${id}`);
    // @ts-ignore
    this.$router.push({
      name: "Detail",
      params: {
        id: id
      }
    });
  }
  private addHero(): void {
    if(!this.newHero){return;}
    this.$store.state.HeroList.push({
      name: this.newHero,
      id: this.$store.state.idNum
    });
    this.$store.commit(
      "addMessage",
      `added hero w/ id=${this.$store.state.idNum}`
    );
    this.$store.commit("freshTop");
    this.$store.state.idNum++;
  }
  private deleteHero(id: number): void {
    this.$store.commit("deleteHero", id);
    this.$store.commit("freshTop");
    this.$store.commit("addMessage", `deleted hero id=${id}`);
  }
  private created(): void {
    this.lists = this.$store.state.HeroList;
    this.$store.commit("addMessage", "fetched heroes");
  }

  //问题
  // private beforeRouteEnter(to:string, from:string, next:any):void {
  //   console.log(next)
  //   next((vm:any) => {
  //     console.log(vm)
  //   });
  // }
}
